<template>
  <div>
    <div v-if="loading">
      <b-progress :value="loading? 50 : 100" :max="100" animated></b-progress>
    </div>
    <b-row v-if="!loading" class="animated fadeIn">
      <b-col :lg="12">
        <b-button class="mb-2 mr-2" @click="refresh">Refresh</b-button>
        <b-button class="mb-2" @click="create" variant="primary">Add new admin</b-button>
      </b-col>

      <b-col :lg="current ? 6 : 12">
        <c-table
          :table-data="items"
          :fields="fields"
          hover
          caption="Admin"
          :rowClicked="rowClicked"
        ></c-table>
      </b-col>

      <b-col lg="6" v-if="current" class="animated fadeIn">
        <b-card class="card-accent-info" >
          <b-form>
            <b-form-group v-for="field in fields" :key="field.key">
              <b-input-group v-if="['ID', 'status'].indexOf(field.key) === -1">
                <b-input-group-prepend>
                  <b-input-group-text>{{field.label ? field.label : field.key}}</b-input-group-text>
                </b-input-group-prepend>
                <b-form-input disabled v-if="current.Point != -1" type="text" v-model="current[field.key]"></b-form-input>
                <b-form-input v-if="current.Point === -1" type="text" v-model="current[field.key]"></b-form-input>
              </b-input-group>
            </b-form-group>
             <b-input-group style="margin-bottom:3%">
              <b-input-group-prepend >
                <b-input-group-text >Phone number</b-input-group-text>
              </b-input-group-prepend>
              <b-form-input v-if="current.Point === -1" type="text" v-model="current.PhoneNumber"></b-form-input>
              <b-form-input disabled v-if="current.Point != -1" type="text" v-model="current.PhoneNumber"></b-form-input>
            </b-input-group>
            <b-input-group v-show="current.Point === -1" style="margin-bottom:3%">
              <b-input-group-prepend>
                <b-input-group-text>Password</b-input-group-text>
              </b-input-group-prepend>
              <b-form-input type="text" v-model="current.Password"></b-form-input>
            </b-input-group>
            
            <div
              class="btn-group form-actions animated fadeIn"
              v-if="!detailMessage && current.Point != -1"
            ></div>
            <div class="form-actions animated fadeIn" v-if="!detailMessage && current.Point===-1">
              <b-button @click="add" type="submit" block variant="outline-primary">Add</b-button>
            </div>
            <div v-if="detailMessage">
              <b-button @click="hideWarning" block variant="outline-warning">{{detailMessage}}</b-button>
            </div>
          </b-form>
        </b-card>
      </b-col>
    </b-row>
  </div>
</template>

<script>
import cTable from "@/views/base/Table.vue";
import { Switch as cSwitch } from "@coreui/vue";
import { openapi, methods, routes } from "@/openapi";

export default {
  name: "tables",
  components: { cTable, cSwitch },
  data: () => {
    return {
      loading: true,
      items: [],
      current: null,
      detailMessage: "",
      fields: [
        { key: "UserName", sortable: true },
        { key: "Email", sortable: true }, //, label: "hello"
        { key: "Fullname", sortable: true },
        { key: "Address", sortable: true }
      ]
    };
  },
  mounted: function() {
    this.refresh();
  },
  methods: {
    refresh: function() {
      this.loading = true;
      this.current = null;
      openapi(methods.GET, routes.ADMINS).then(data => {
        this.loading = false;
        this.items = data;
      });
    },
    rowClicked: function(user, i) {
      this.current = user;
    },
    hideSuccess: function() {
      setTimeout(() => {
        this.detailMessage = "";
      }, 1000);
    },
    hideWarning: function() {
      this.detailMessage = "";
    },
    create: function() {
      this.current = {
        Point: -1,
        Status: true
      };
    },
    add: function() {
      this.detailMessage = "Adding...";
      this.current.Point = 0;
      this.current.ID = openapi(methods.POST, routes.ACCOUNT, {
        RoleName: "Admin",
        Password: this.current.Password,
        Account: this.current
      }).then(data => {
        if (data.ModelState) {
          this.current.Point = -1;
          this.detailMessage =
            data.ModelState[Object.keys(data.ModelState)[0]][0] + " Try again";
        } else {
          this.detailMessage = "Added successfully!";
          this.items.push(this.current);
          this.hideSuccess();
        }
      });
    }
  }
};
</script>
